@font-face {
    font-family: 'starjedi';
    src: url('./fonts/Starjedi.ttf');
}

@font-face {
    font-family: 'starjhol';
    src: url('./fonts/Starjhol.ttf');
}

@font-face {
    font-family: 'starjout';
    src: url('./fonts/Starjout.ttf');
}

@font-face {
    font-family: 'jedi-subtitle';
    src: url('./fonts/Typo Grotesk Demo.otf');
}

@font-face {
    font-family: 'biko';
    src: url('./fonts/Biko_Regular.otf');
}

body {
  margin: 0;
  padding: 0;
  font-family: starjedi, Arial, Helvetica, sans-serif;
  font-size: 14px;
  color: white;
  letter-spacing: 1px;
}

button {
  background-color: #f7e655;
  color: black;
  font-family: 'starjedi';
  border: none;
  cursor: pointer;
}

button:focus {
  outline: none;
}

button:active {
  background-color: #f0eab8;
}

button:disabled {
  background-color: #f0eab8;
  font-style: italic;
}

#root {
  height: 100%;
}

.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  min-height: 100vh;
  background-color: black;
}

.Header,
.Footer {
  padding: 5px 20px;
  color: #f7e655;
  background-color: black;
  text-align: center;
}

.Header .title {
  height: 35px;
  line-height: 35px;
  font-size: 35px;
  font-family: 'starjhol';
}

.Header .subtitle {
  font-family: 'jedi-subtitle';
  font-size: 20px;
  letter-spacing: 2px;
  color: white;
}

.content {
  -ms-flex-positive: 1;
      flex-grow: 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding: 30px 0;
  background-image: url('./images/stars.png');
  background-size: cover;
}

.AddChar form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  height: 40px;
  padding: 10px;
}

.AddChar input {
  height: 24px;
  line-height: 24px;
  margin-right: 10px;
  padding-left: 10px;
  border: none;
  border-top: 1px solid #f7e655;
  border-bottom: 1px solid #f7e655;
  background-color: transparent;
  -webkit-transition: padding .2s linear;
  -o-transition: padding .2s linear;
  transition: padding .2s linear;
  color: #f7e655;
  font-family: "starjedi";
}

.AddChar input:focus {
  padding: 5px 0 5px 10px;
  outline: none;
  border-top: 1px solid #e0d24d;
  border-bottom: 1px solid #e0d24d;
}

.CharList {
  -ms-flex-positive: 1;
      flex-grow: 1;
  list-style: none;
}

.CharList li {
  cursor: pointer;
}

.CharList li.dead span:first-child {
  text-decoration: line-through;
  font-style: italic;
  color: #e04d4d;
}

.CharList li {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.CharList li span:last-child {
  margin-left: 10px;
  color: #e71919;
}

.Footer {
  bottom: 0;
  text-align: center;
  font-size: 12px;
  font-family: 'starjedi';
}


